.loading {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: $primary-color;
    z-index: $diamond-lever;
    display: flex;
    justify-content: center;
    align-items: center;
    &__wrapper {
        display: flex;
        position: relative;
        align-items: center;
        .text {
            width: 100vw;
            text-align: center;
            overflow: hidden;
            position: relative;
            font-weight: bold;
            .logo {
                font-size: 10 * $size;
                display: flex;
                align-items: center;
                position: absolute;
                height: 100%;
                top: 100%;
                left: 50%;
                transform: translateX(-50%);
                .icon {
                    width: 100px;
                    margin-right: 3 * $size;
                }
            }
            .dot {
                font-size: 15 * $size;
                display: inline-block;
                margin: $size * 0.5;
            }
        }
        .loaded {
            text-align: center;
            color: $bg-color;
            font-weight: 600;
            font-size: 3 * $size;
            @include absoluteCenter("hor");
            top: 150%;
            opacity: 0;
        }
    }
}
@include responsive($break-point-sm) {
    .loading {
        &__wrapper {
            .text {
                .logo {
                    font-size: 5 * $size;
                    .icon {
                        width: 50px;
                        margin-right: $size;
                    }
                }
                .dot {
                    font-size: 15 * $size;
                    display: inline-block;
                    margin: $size * 0.5;
                }
            }
            .loaded {
                font-size: 2 * $size;
            }
        }
    }
}
